import React from "react";
import { NavLink } from "react-router-dom";

function Navbar() {
  return (
    <header>
      <button className="toggle-btn" type="button">
        <span uk-icon="menu"></span>
      </button>
      <div id="menu-bar" className="menu-bar" uk-dropdown="pos: bottom-center; mode: click; animation: uk-animation-slide-top-small; duration: 300; animate-out: true">
        <ul>
          <li className="menu-item">
            <NavLink to="/" className="menu-link" uk-toggle="target: #menu-bar">
              <span uk-icon="home"></span>
              <span className="page-title">Home</span>
            </NavLink>
          </li>
          <li className="menu-item">
            <NavLink to="/About" className="menu-link" uk-toggle="target: #menu-bar">
              <span uk-icon="info"></span>
              <span className="page-title">About Me</span>
            </NavLink>
          </li>
          <li className="menu-item">
            <NavLink to="/Portfolio" className="menu-link" uk-toggle="target: #menu-bar">
              <span uk-icon="thumbnails"></span>
              <span className="page-title">Portfolio</span>
            </NavLink>
          </li>
        </ul>
      </div>
    </header>
  )
}

export default Navbar;