import React from "react";
import { NavLink } from "react-router-dom";

function NotFound() {
  return (
    <section className="notFound">
      <div>
        <img src="/Assets/imgs/404.svg" alt="Page Not Found"/>
        <h3>Oops, Page Not Found</h3>
        <div className="links">
          Return to
          <NavLink to='/'>
            Home
          </NavLink>
        </div>
      </div>
    </section>
  )
}

export default NotFound;