import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";

function ProjectInfo() {

  const { prName } = useParams()

  const [loading, setLoading] = useState(true)
  const [project, setProject] = useState('')

  useEffect(() => {
    axios.get('http://localhost:3000/ProjectsData/Data.json')
    .then(
      res => {
        const prData = res.data.find(pr => pr.path.toLowerCase() === prName.toLowerCase())
        setProject(prData)
        setTimeout(() => {
          setLoading(false)
        }, 100)
      }
    )
  }, [prName])

  return (
    <section className="project-details">
      <div className="section-header uk-container uk-container-small">
        <h1>
          <span>Project</span> Details
        </h1>
        <p>
          <span uk-icon="icon: chevron-left; ratio: 1.1"></span>
          MORE DETAILS ABOUT THE PROJECT.
          <span uk-icon="icon: chevron-right; ratio: 1.1"></span>
        </p>
      </div>
      {
        !loading ?
        <div>
          <div className="pr-info">
            <div className="uk-container uk-container-small">
              <div className="uk-child-width-1-1" uk-grid="true">
                <div className="uk-width-1-3@s">
                  <img src={project.logo}/>
                </div>
                <div className="uk-width-2-3@s pr-abstract">
                  <div>
                    <h3>{project.name}</h3>
                    <p>
                      {project.description}
                    </p>
                    <div className="pr-techs">
                      <h5>Technologies used in the project!</h5>
                      {
                        project.techs.map(
                          tech => <span key={tech.id} className="uk-badge">{tech.tech}</span>
                        )
                      }
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="gallery">
            <div className="uk-container uk-container-small">
              <h3>
                <span uk-icon="icon: info; ratio: 0.8"></span>
                Some Screens From The Project.
              </h3>
              <div className="uk-child-width-1-2 uk-child-width-1-3@m uk-text-center" uk-grid="true" uk-lightbox="animation: slide">
                {
                  project.imgs.map(
                    img =>
                    <div key={img.id}>
                      <div className="uk-card uk-card-default uk-card-body uk-padding-remove">
                        <a href={img.src}>
                          <div className="img-view" style={
                            {
                              backgroundImage: `url(${img.src})`
                            }
                          }/>
                        </a>
                      </div>
                    </div>
                  )
                }
              </div>
            </div>
          </div>
        </div>
        :
        null
      }
    </section>
  )
}

export default ProjectInfo;